import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    meta: {
      title: "",
      action: "/",
    },
    component: Home,
  },
  {
    path: "/red-one-pc/",
    redirect: "/",
  },
  {
    path: "/Home",
    redirect: "/",
  },
  {
    path: "/Store",
    name: "Store",
    meta: {
      title: "",
      action: "/Store",
    },
    component: () =>
      import(/* webpackChunkName: "Store" */ "../views/Store.vue"),
  },
  {
    path: "/AboutUs",
    name: "AboutUs",
    meta: {
      title: "",
      action: "/AboutUs",
    },
    component: () =>
      import(/* webpackChunkName: "AboutUs" */ "../views/AboutUs.vue"),
  },
  {
    path: "/Announcement",
    name: "Announcement",
    meta: {
      title: "",
      action: "/Announcement",
    },
    component: () =>
      import(
        /* webpackChunkName: "Announcement" */ "../views/Announcement.vue"
      ),
  },
  {
    path: "/Detail",
    name: "Detail",
    meta: {
      title: "",
      action: "/Store",
    },
    component: () =>
      import(/* webpackChunkName: "Detail" */ "../views/Detail.vue"),
  },
  {
    path: "/Merchant",
    name: "Merchant",
    meta: {
      title: "",
      action: "/Store",
    },
    component: () =>
      import(/* webpackChunkName: "Merchant" */ "../views/Merchant.vue"),
  },
  {
    path: "/Login",
    name: "Login",
    meta: {
      title: "红One入口",
      action: "/Login",
    },
    component: () =>
      import(/* webpackChunkName: "Login" */ "../views/Login.vue"),
  },
  {
    path: "/User",
    name: "User",
    meta: {
      title: "用户中心",
      action: "/User",
    },
    component: () => import(/* webpackChunkName: "User" */ "../views/User.vue"),
  },
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes,
});
router.onError((error) => {
  const pattern = /Loading chunk (\d)+ failed/g;
  const isChunkLoadFailed = error.message.match(pattern);
  const targetPath = router.history.pending.fullPath;
  if (isChunkLoadFailed) {
    router.replace(targetPath);
  }
});

export default router;
