import Vue from "vue";
import router from "./router";
import store from "./store";

import {
  Carousel,
  CarouselItem,
  Select,
  DatePicker,
  Button,
  Option,
  Pagination,
  Container,
  Aside,
  Header,
  Main,
  Menu,
  Submenu,
  MenuItem,
  MenuItemGroup,
  RadioGroup,
  RadioButton,
  Drawer
} from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import App from "./App.vue";

Vue.use(Carousel)
  .use(CarouselItem)
  .use(Select)
  .use(DatePicker)
  .use(Button)
  .use(Option)
  .use(Pagination)
  .use(Container)
  .use(Aside)
  .use(Header)
  .use(Main)
  .use(Menu)
  .use(Submenu)
  .use(MenuItem)
  .use(MenuItemGroup)
  .use(RadioGroup)
  .use(RadioButton)
  .use(Drawer)



Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
